        {title: 'Co-Creation', info: 'This series of initiatives aims to create a positive ecosystem, rewarding our holders, supporting environmental causes, and encouraging more people to join our project and become part of animal protection. It\'s a collective effort, so let\'s work together to create a better future.'}
