    const connectWallet = () => {//link MetaMask
      if (state.ethAccount) return;
      window.ethereum.request({ method: 'eth_requestAccounts' }).then((res) => {
        // console.log(res, 'MetaMask address')
        state.ethAccount = res[0]
      }).catch((err) => {
        console.log(err)
        if (err.code == 4001) {
          alert('You have cancelled the connection')
        } else if(err.code == -32002) {
          alert('The connection has been suspended. Please log in and connect in the pop-up window, or check if you have disabled the browser\'s pop-up window!')
        } else {
          alert(err.message)
        }
      })
    };
